<template>
<div class="screening-bd">
  <el-carousel indicator-position="outside">
    <!-- <el-carousel-item v-for="items in groups" :key="items"> -->
    <!-- <el-carousel-item v-for="(item, idx) of items1" :key="idx"> -->
    <el-carousel-item 
    v-for="(items, idx) of groups"
    :key="idx">
      <ul>
        <li v-for="(item, sidx) of items" :key="sidx">
          <ul>
            <li class="poster">
              <img :src="item.images.small" alt="picture">
              <!-- <div :style="getPicStyle(item.images.small)"></div> -->
            </li>
            <li class="title">{{ item.title }}</li>
            <li class="rating">⭐⭐⭐⭐⭐</li>
            <li class="ticket_btn">
              <el-button type="primary">选座购票</el-button>
            </li>
          </ul>
        </li>
      </ul>
    </el-carousel-item>
  </el-carousel>
</div>  
</template>

<script>
import axios from 'axios'
import $ from 'jquery'

export default {
  data() {
    // console.log('return data')
    return {
      groups:[]
    };
  },

  mounted: function () {
    this.$nextTick(function () {
      // this.getGruopsData(this.pushGruopsData)
      this.getGruopsDataByJsonP(this.pushGruopsData)
    })
  },

  methods: {
    getGruopsData: function (cb) {
      axios.get('/doubanapi/v2/movie/in_theaters')
        .then((response) => {
          // console.log('get res')
          cb(response)
        })
    },

    getGruopsDataByJsonP: function (cb) {
      $.ajax({
        type: "get",
        async: false,
        url: "https://api.douban.com/v2/movie/in_theaters",
        dataType: "jsonp",
        jsonp: "callback", 
        jsonpCallback: "success_jsonpCallback", 
        success: function (res) {
          console.log('get res by JsonP')
          cb(res)
        },
        error: function () {
          console.log('fail')
        }
      });
    },

    pushGruopsData: function (response) {
      console.log(response)
      const subjects = response.subjects
      let arr = []
      for (let i = 0, j = 0, length = subjects.length; i < length; i++) {
        arr.push(subjects[i])
        j++
        if (j === 5) {
          this.groups.push(arr)
          arr = []
          j = 0
        }
      }
    },

    getPicStyle: function (url) {
      return `background : url( ${url} ) no-repeat center`
    }
  },

  computed: {

  }
};
</script>

<style lang="stylus" scoped>
.screening-bd
  li
    display inline-block
    background url('')
    ul>li
      display block
      img
        width 115px
        height 161px
</style>





